@use 'base/mixins' as m;
@use 'base/variables' as v;

.hero{
    
    @include m.container(430);
    @include m.tablet{
        padding-top: m.em(480);
    }

    @include m.desktop{
        padding-top: m.em(80);
        height: m.rem(480);
        display: flex;
        align-items: center;
    }

    @include m.desktopXL{
        height: m.rem(580);
    }

    &__illustration{
        position: absolute;
        z-index: -1;
        top: m.rem(140);
        right: 0;
    }

    &__picture{
        position: relative;
        max-width: m.rem(400);
        margin-left: auto;
        
        &::after{
            content: '';
            display: block;
            position: absolute;
            width: 80%;
            height: 75%;
            background-color: v.$soft-blue;
            top: 30%;
            right: 0;
            border-bottom-left-radius: m.rem(100);

            @include m.desktopXL{
                width: 90%;
                border-bottom-left-radius: m.rem(150);
            }
        }

        @include m.tablet{
            max-width: m.rem(480);
        }

        @include m.desktopXL{
            max-width: m.rem(560);
        }
    }

    &__img{
        @include m.imagen;
        position: relative;
        z-index: 1;

        @include m.tablet{
            right: m.rem(100);
        }
    }

    &__texts{
        text-align: center;
        margin-top: m.em(40);
        
        @include m.desktop{
            width: 35%;
            text-align: initial;
            margin-top: 0;
        }
    }

    &__heading{
        font-size: m.rem(28);
        color: v.$very-dark-blue;
        line-height: 1.5;

        @include m.desktop{
            font-size: m.rem(30);
        }

        @include m.desktopXL{
            font-size: m.rem(40);
        }
    }

    &__paragraph{
        color: v.$grayish-blue;
        line-height: 1.7;
        margin: m.em(30) auto;
        width: 90%;

        @include m.desktop{
            width: 100%;
        }
    }

    &__button{
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: m.em(10);
        text-align: center;
    }

    &__cta{
        text-decoration: none;
        display: inline-block;
        border-radius: m.rem(5);
        padding: m.em(15) 0;
        width: 50%;
        box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.2);
        font-weight: 500;
        border: 2px solid transparent;
        transition: all .3s;

        &--1{
            background-color: v.$soft-blue;
            color: v.$white;

            &:hover{
                border: 2px solid v.$soft-blue;
                color: v.$soft-blue;
            }
        }

        &--2{
            background-color: v.$white;
            color: v.$very-dark-blue;

            &:hover{
                border: 2px solid v.$very-dark-blue;
                color: v.$very-dark-blue;
            }
        }

        &:hover{
            background-color: transparent;
        }
    }
}