@use 'base/mixins' as m;
@use 'base/variables' as v;

.features{
    padding-top: m.em(150);

    &__heading{
        text-align: center;
        @include m.container(0);
        @include m.desktop{
            max-width: m.rem(700);
        }
    }

    &__title{
        color: v.$very-dark-blue;
    
        @include m.desktop{
            font-size: m.rem(30);
        }
    }

    &__paragraph{
        color: v.$grayish-blue;
        margin: m.em(20) 0;
        line-height: 1.7;

        @include m.desktopXL{
            width: 70%;
        }

        &--main{
            @include m.desktop{
                margin: m.em(20) auto;
            }
        }
    }

    &__tabs{
        padding-top: m.em(20);
    }

    &__items{
        list-style-type: none;
        text-align: center;
        @include m.container(0);

        &:nth-child(1){
            border-top: 1px solid v.$grayish-blue;
        }

        @include m.desktop{
            display: flex;
            align-items: center;
            justify-content: space-between;
            max-width: m.rem(700);
            border-bottom: 1px solid v.$grayish-blue;

            &:nth-child(1){
                border-top: none;
            }
        }
    }

    &__item{
        padding: m.em(20) 0;
        border-bottom: 1px solid v.$grayish-blue;
        font-weight: 500;
        color: v.$grayish-blue;
        position: relative;
        cursor: pointer;
        transition: color .3s;

        &.active{
            color: v.$very-dark-blue;

            &::after{
                content: '';
                position: absolute;
                left: 0;
                right: 0;
                bottom: 0;
                display: block;
                height: m.rem(4);
                width: 50%;
                background-color: v.$soft-Red;
                margin: 0 auto;

                @include m.desktop{
                    width: 100%;
                }
            }
        }

        @include m.desktop{
            border: none;
            width: m.rem(200);
            padding: m.em(30) 0;
        }

        &:hover{
            color: v.$soft-Red;
        }
    }

    &__content{
        margin-top: m.em(80);

        @include m.desktop{
            min-height: m.rem(450);
        }
    }

    &__info{
        margin-top: m.em(40);
        display: none;
        opacity: 0;
        transition: all .3s;

        &.active{
            display: grid;
            grid-template-columns: 5% 1fr 5%;
            grid-template-rows: max-content max-content;

            @include m.desktop{
                grid-template-columns: 5% 1fr 1fr 5%;
                grid-template-rows: max-content;
            }
        }

        &.visible{
            opacity: 1;
        }

        &::after{
            content: '';
            width: 80%;
            height: 90%;
            background-color: v.$soft-blue;
            grid-column: 1/3;
            grid-row: 1/2;
            transform: translateY(30%);
            z-index: -1;
            border-bottom-right-radius: m.rem(100);

            @include m.telefono{
                width: 60%;
                max-height: m.rem(320);
            }

            @include m.desktop{
                width: 60%;
                height: 85%;
            }
        }
    }

    &__picture{
        grid-column: 2/3;
        grid-row: 1/2;
        justify-self: center;
    }

    &__texts{
        margin-top: m.em(100);
        text-align: center;
        grid-column: 2/3;

        @include m.desktop{
            grid-column: 3/4;
            margin: 0;
            align-self: center;
            text-align: initial;
            margin-left: m.em(40);
        }

        @include m.desktopXL{
            margin-left: m.em(80)
        }
    }

    &__img{
        @include m.imagen;

        @include m.desktop{
            object-fit: contain;
        }

    }

    &__cta{
        display: inline-block;
        padding: m.em(10) m.em(20);
        background-color: v.$soft-blue;
        color: v.$white;
        text-decoration: none;
        border-radius: m.rem(5);
        box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.2);
    }
}