@use 'base/mixins' as m;
@use 'base/variables' as v;

.footer{
    background-color: v.$very-dark-blue;
    
    &__container{
        @include m.container(40);
        padding-bottom: m.em(30);
        text-align: center;

        @include m.desktop{
            display: flex;
            align-items: center;
        }
    }

    &__links{
        @include m.desktop{
            display: flex;
            align-items: center;
            gap: m.em(30);
            margin-left: m.em(80);
        }
    }

    &__link{
        display: block;
        text-decoration: none;
        color: v.$white;
        text-transform: uppercase;
        letter-spacing: 2px;
        font-size: m.rem(14);
        margin: m.em(30) 0;
        transition: color .3s;

        @include m.desktop{
            margin: 0;
        }

        &:hover{
            color: v.$soft-Red;
        }
    }

    &__social{
        color: v.$white;
        font-size: m.rem(34);
        display: flex;
        align-items: center;
        justify-content: center;
        gap: m.em(10);
        cursor: pointer;
        
        @include m.desktop{
            margin-left: auto;
            font-size: m.rem(30);
        }

        & i:hover{
            transition: color .3s;
            color: v.$soft-Red;
        }
    }
}