@use 'variables' as v;

@mixin container($padding){
    width: 90%;
    max-width: rem(1200);
    margin: 0 auto;

    @if($padding > 0){
        padding-top: em($padding);
    }
}

@mixin imagen{
    width: 100%;
    height: 100%;
    display: block;
}

@mixin telefono{
    @media (min-width: v.$telefono){
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet){
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop){
        @content;               
    }
}

@mixin desktopXL {
    @media (min-width: v.$desktopXL){
        @content;
    }
}

@function rem($px){
    @return calc($px / 16)+rem;
}

@function em($px){
    @return calc($px / 16)+em;
}
