@use 'base/mixins' as m;
@use 'base/variables' as v;

.contact{
    background-color: v.$soft-blue;

    &__container{
        @include m.container(60);
        padding-bottom: m.em(40);
        text-align: center;

        @include m.tablet{
            max-width: m.rem(600);
        }
    }

    &__stats{
        text-transform: uppercase;
        color: v.$white;
        letter-spacing: 3px;
        font-size: m.rem(14);
    }

    &__heading{
        margin: m.em(10) 0 m.em(20);
        color: v.$white;
        font-weight: 400;

        @include m.desktop{
            width: 70%;
            margin: m.em(10) auto m.em(20);
            font-weight: 500;
            font-size: m.rem(30);
        }
    }

    &__form{
        @include m.desktop{
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
        }
    }

    &__formgroup{
        width: 100%;
        position: relative;

        @include m.desktop{
            width: 65%;
        }

        &.error{
            .contact__input{
                border: 2px solid v.$soft-Red;
            }
            
            .contact__icon{
                visibility: visible;
            }
        }
    }

    &__input{
        display: block;
        width: 100%;
        height: m.rem(53);
        padding: m.em(15) m.em(50) m.em(15) m.em(15);
        border: none;
        outline: none;
        border-radius: m.rem(5);
        font-family: inherit;
        font-size: m.rem(16);
        box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.2);
    }

    &__icon{
        visibility: hidden;
        position: absolute;
        top: m.rem(14);
        right: m.rem(20);
        color: v.$soft-Red;
        font-size: m.rem(24);
        font-weight: 500;
    }

    &__cta{
        display: block;
        width: 100%;
        height: m.rem(53);
        margin-top: m.em(20);
        padding: m.em(15) 0;
        border: none;
        outline: none;
        border-radius: m.rem(5);
        background-color: v.$soft-Red;
        color: v.$white;
        font-family: inherit;
        font-size: m.rem(16);
        font-weight: 500;
        box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.2);
        border: 2px solid transparent;
        cursor: pointer;
        transition: all .3s;

        @include m.desktop{
            margin: 0;
            width: 30%;
            text-align: center;
            padding: m.em(15);
        }

        &:hover{
            background-color: v.$white;
            color: v.$soft-Red;
            border: 2px solid v.$soft-Red;
        }
    }

    &__error{
        background-color: v.$soft-Red;
        padding: m.em(10) m.em(15);
        border-bottom-left-radius: m.rem(5);
        border-bottom-right-radius: m.rem(5);
        text-align: initial;
        font-size: m.rem(13);
        color: v.$white;
        font-weight: 500;
        font-style: italic;
    }
}