@use 'base/mixins' as m;
@use 'base/variables' as v;

.navbar{
    box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.1);
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    background-color: v.$white;

    &__container{
        @include m.container(0);
        height: m.rem(80);
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__img{
        @include m.imagen;
    }

    &__nav{
        position: fixed;
        z-index: 100;
        inset: 0;
        background-color: hsl(229, 31%, 21%);
        filter: opacity(95%);
        padding: 0 m.em(30) m.em(50);
        display: flex;
        flex-direction: column;
        transform: translateX(100%);
        transition: transform .4s;
    
        @include m.tablet{
            transform: translateX(0);
            background-color: transparent;
            padding: 0;
            position: unset;
        }
    }

    &__menu{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: m.rem(80);
        @include m.tablet{
            display: none;
        }
    }

    &__close{
        color: v.$white;
        cursor: pointer;
    }

    &__links{
        margin-top: m.em(30);
        text-align: center;
        :nth-child(1){
            border-top: 1px solid v.$grayish-blue;
        }

        @include m.tablet{
            display: flex;
            align-items: center;
            gap: m.em(30);
            margin-top: 0;


            :nth-child(1){
                border-top: none;
            }
        }
    }

    &__link{
        display: block;
        text-decoration: none;
        color: v.$white;
        text-transform: uppercase;
        padding: m.em(20) 0;
        border-bottom: 1px solid v.$grayish-blue;
        letter-spacing: 2px;
        font-size: m.rem(18);
        transition: all .3s;

        @include m.tablet{
            color: v.$very-dark-blue;
            font-size: m.rem(14);
            letter-spacing: 1px;
            border: none;
            padding: 0;
            font-weight: 500;
        }

        &--cta{
            margin-top: m.em(20);
            border: 2px solid v.$white;
            border-radius: m.rem(5);
            padding: m.em(10) 0;

            @include m.tablet{
                margin-top: 0;
                background-color: v.$soft-Red;
                color: v.$white;
                padding: m.em(10) m.em(30);
            }

            &:hover{
                background-color: transparent;
                border: 2px solid v.$soft-Red;
            }
        }

        &:hover{
            color: v.$soft-Red;
        }

    }

    &__social{
        margin-top: auto;
        display: flex;
        justify-content: center;
        gap: m.em(20);
        color: v.$white;
        font-size: m.rem(34);

        @include m.tablet{
            display: none;
        }
    }

    &__hamburguer{
        color: v.$very-dark-blue;
        cursor: pointer;

        @include m.tablet{
            display: none;
        }
    }

    &__icon{
        font-size: m.rem(30);
    }

    &.active{
        .navbar__picture,
        .navbar__hamburguer{
            display: none;
        }

        .navbar__nav{
            transform: translateX(0);
        }
    }
}
