@use 'base/mixins' as m;
@use 'base/variables' as v;

.download{
    @include m.container(120);

    &__heading{
        text-align: center;
    }

    &__title{
        color: v.$very-dark-blue;
        &--main{
            @include m.desktop{
                font-size: m.rem(30);
            }
        }
    }

    &__paragraph{
        color: v.$grayish-blue;
        margin: m.em(20) 0;
        line-height: 1.7;

        @include m.desktop{
            max-width: m.rem(500);
            margin: 0 auto;
        }
    }

    &__options{
        margin-top: m.em(40);

        @include m.tablet{
            display: flex;
            flex-wrap: wrap;
            gap: m.em(20);
            align-items: flex-start;
            justify-content: space-evenly;
        }
    }

    &__option{
        max-width: m.em(300);
        margin: 0 auto;
        margin-bottom: m.em(30);
        box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.2);
        border-radius: m.rem(5);
        text-align: center;
        padding: m.em(40) 0 m.em(30);

        @include m.tablet{
            margin: 0;

            &--2{
                margin-top: m.em(40);
            }

            &--3{
                margin-top: m.em(80);
            }
        }
    }

    &__title{
        color: v.$very-dark-blue;
        padding: m.em(30) 0 m.em(10);
    }

    &__text{
        color: v.$grayish-blue;
    }

    &__dots{
        width: 100%;
        padding: m.em(40) 0 m.em(20);
    }

    &__cta{
        display: block;
        background-color: v.$soft-blue;
        text-decoration: none;
        color: v.$white;
        padding: m.em(15) 0;
        width: 90%;
        margin: 0 auto;
        border-radius: m.rem(5);
        font-weight: 500;
        box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.2);
        border: 2px solid transparent;
        transition: all .3s;

        &:hover{
            background-color: transparent;
            border: 2px solid v.$soft-blue;
            color: v.$soft-blue;
        }
    }
}