@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@400;500&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Rubik", sans-serif;
}

.navbar {
  box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.1);
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  background-color: hsl(0, 0%, 100%);
}
.navbar__container {
  width: 90%;
  max-width: 75rem;
  margin: 0 auto;
  height: 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar__img {
  width: 100%;
  height: 100%;
  display: block;
}
.navbar__nav {
  position: fixed;
  z-index: 100;
  inset: 0;
  background-color: hsl(229, 31%, 21%);
  filter: opacity(95%);
  padding: 0 1.875em 3.125em;
  display: flex;
  flex-direction: column;
  transform: translateX(100%);
  transition: transform 0.4s;
}
@media (min-width: 768px) {
  .navbar__nav {
    transform: translateX(0);
    background-color: transparent;
    padding: 0;
    position: unset;
  }
}
.navbar__menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 5rem;
}
@media (min-width: 768px) {
  .navbar__menu {
    display: none;
  }
}
.navbar__close {
  color: hsl(0, 0%, 100%);
  cursor: pointer;
}
.navbar__links {
  margin-top: 1.875em;
  text-align: center;
}
.navbar__links :nth-child(1) {
  border-top: 1px solid hsl(229, 8%, 60%);
}
@media (min-width: 768px) {
  .navbar__links {
    display: flex;
    align-items: center;
    gap: 1.875em;
    margin-top: 0;
  }
  .navbar__links :nth-child(1) {
    border-top: none;
  }
}
.navbar__link {
  display: block;
  text-decoration: none;
  color: hsl(0, 0%, 100%);
  text-transform: uppercase;
  padding: 1.25em 0;
  border-bottom: 1px solid hsl(229, 8%, 60%);
  letter-spacing: 2px;
  font-size: 1.125rem;
  transition: all 0.3s;
}
@media (min-width: 768px) {
  .navbar__link {
    color: hsl(229, 31%, 21%);
    font-size: 0.875rem;
    letter-spacing: 1px;
    border: none;
    padding: 0;
    font-weight: 500;
  }
}
.navbar__link--cta {
  margin-top: 1.25em;
  border: 2px solid hsl(0, 0%, 100%);
  border-radius: 0.3125rem;
  padding: 0.625em 0;
}
@media (min-width: 768px) {
  .navbar__link--cta {
    margin-top: 0;
    background-color: hsl(0, 94%, 66%);
    color: hsl(0, 0%, 100%);
    padding: 0.625em 1.875em;
  }
}
.navbar__link--cta:hover {
  background-color: transparent;
  border: 2px solid hsl(0, 94%, 66%);
}
.navbar__link:hover {
  color: hsl(0, 94%, 66%);
}
.navbar__social {
  margin-top: auto;
  display: flex;
  justify-content: center;
  gap: 1.25em;
  color: hsl(0, 0%, 100%);
  font-size: 2.125rem;
}
@media (min-width: 768px) {
  .navbar__social {
    display: none;
  }
}
.navbar__hamburguer {
  color: hsl(229, 31%, 21%);
  cursor: pointer;
}
@media (min-width: 768px) {
  .navbar__hamburguer {
    display: none;
  }
}
.navbar__icon {
  font-size: 1.875rem;
}
.navbar.active .navbar__picture,
.navbar.active .navbar__hamburguer {
  display: none;
}
.navbar.active .navbar__nav {
  transform: translateX(0);
}

.hero {
  width: 90%;
  max-width: 75rem;
  margin: 0 auto;
  padding-top: 26.875em;
}
@media (min-width: 768px) {
  .hero {
    padding-top: 30em;
  }
}
@media (min-width: 1024px) {
  .hero {
    padding-top: 5em;
    height: 30rem;
    display: flex;
    align-items: center;
  }
}
@media (min-width: 1200px) {
  .hero {
    height: 36.25rem;
  }
}
.hero__illustration {
  position: absolute;
  z-index: -1;
  top: 8.75rem;
  right: 0;
}
.hero__picture {
  position: relative;
  max-width: 25rem;
  margin-left: auto;
}
.hero__picture::after {
  content: "";
  display: block;
  position: absolute;
  width: 80%;
  height: 75%;
  background-color: hsl(231, 69%, 60%);
  top: 30%;
  right: 0;
  border-bottom-left-radius: 6.25rem;
}
@media (min-width: 1200px) {
  .hero__picture::after {
    width: 90%;
    border-bottom-left-radius: 9.375rem;
  }
}
@media (min-width: 768px) {
  .hero__picture {
    max-width: 30rem;
  }
}
@media (min-width: 1200px) {
  .hero__picture {
    max-width: 35rem;
  }
}
.hero__img {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
  z-index: 1;
}
@media (min-width: 768px) {
  .hero__img {
    right: 6.25rem;
  }
}
.hero__texts {
  text-align: center;
  margin-top: 2.5em;
}
@media (min-width: 1024px) {
  .hero__texts {
    width: 35%;
    text-align: initial;
    margin-top: 0;
  }
}
.hero__heading {
  font-size: 1.75rem;
  color: hsl(229, 31%, 21%);
  line-height: 1.5;
}
@media (min-width: 1024px) {
  .hero__heading {
    font-size: 1.875rem;
  }
}
@media (min-width: 1200px) {
  .hero__heading {
    font-size: 2.5rem;
  }
}
.hero__paragraph {
  color: hsl(229, 8%, 60%);
  line-height: 1.7;
  margin: 1.875em auto;
  width: 90%;
}
@media (min-width: 1024px) {
  .hero__paragraph {
    width: 100%;
  }
}
.hero__button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.625em;
  text-align: center;
}
.hero__cta {
  text-decoration: none;
  display: inline-block;
  border-radius: 0.3125rem;
  padding: 0.9375em 0;
  width: 50%;
  box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.2);
  font-weight: 500;
  border: 2px solid transparent;
  transition: all 0.3s;
}
.hero__cta--1 {
  background-color: hsl(231, 69%, 60%);
  color: hsl(0, 0%, 100%);
}
.hero__cta--1:hover {
  border: 2px solid hsl(231, 69%, 60%);
  color: hsl(231, 69%, 60%);
}
.hero__cta--2 {
  background-color: hsl(0, 0%, 100%);
  color: hsl(229, 31%, 21%);
}
.hero__cta--2:hover {
  border: 2px solid hsl(229, 31%, 21%);
  color: hsl(229, 31%, 21%);
}
.hero__cta:hover {
  background-color: transparent;
}

.features {
  padding-top: 9.375em;
}
.features__heading {
  text-align: center;
  width: 90%;
  max-width: 75rem;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .features__heading {
    max-width: 43.75rem;
  }
}
.features__title {
  color: hsl(229, 31%, 21%);
}
@media (min-width: 1024px) {
  .features__title {
    font-size: 1.875rem;
  }
}
.features__paragraph {
  color: hsl(229, 8%, 60%);
  margin: 1.25em 0;
  line-height: 1.7;
}
@media (min-width: 1200px) {
  .features__paragraph {
    width: 70%;
  }
}
@media (min-width: 1024px) {
  .features__paragraph--main {
    margin: 1.25em auto;
  }
}
.features__tabs {
  padding-top: 1.25em;
}
.features__items {
  list-style-type: none;
  text-align: center;
  width: 90%;
  max-width: 75rem;
  margin: 0 auto;
}
.features__items:nth-child(1) {
  border-top: 1px solid hsl(229, 8%, 60%);
}
@media (min-width: 1024px) {
  .features__items {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 43.75rem;
    border-bottom: 1px solid hsl(229, 8%, 60%);
  }
  .features__items:nth-child(1) {
    border-top: none;
  }
}
.features__item {
  padding: 1.25em 0;
  border-bottom: 1px solid hsl(229, 8%, 60%);
  font-weight: 500;
  color: hsl(229, 8%, 60%);
  position: relative;
  cursor: pointer;
  transition: color 0.3s;
}
.features__item.active {
  color: hsl(229, 31%, 21%);
}
.features__item.active::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  display: block;
  height: 0.25rem;
  width: 50%;
  background-color: hsl(0, 94%, 66%);
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .features__item.active::after {
    width: 100%;
  }
}
@media (min-width: 1024px) {
  .features__item {
    border: none;
    width: 12.5rem;
    padding: 1.875em 0;
  }
}
.features__item:hover {
  color: hsl(0, 94%, 66%);
}
.features__content {
  margin-top: 5em;
}
@media (min-width: 1024px) {
  .features__content {
    min-height: 28.125rem;
  }
}
.features__info {
  margin-top: 2.5em;
  display: none;
  opacity: 0;
  transition: all 0.3s;
}
.features__info.active {
  display: grid;
  grid-template-columns: 5% 1fr 5%;
  grid-template-rows: max-content max-content;
}
@media (min-width: 1024px) {
  .features__info.active {
    grid-template-columns: 5% 1fr 1fr 5%;
    grid-template-rows: max-content;
  }
}
.features__info.visible {
  opacity: 1;
}
.features__info::after {
  content: "";
  width: 80%;
  height: 90%;
  background-color: hsl(231, 69%, 60%);
  grid-column: 1/3;
  grid-row: 1/2;
  transform: translateY(30%);
  z-index: -1;
  border-bottom-right-radius: 6.25rem;
}
@media (min-width: 480px) {
  .features__info::after {
    width: 60%;
    max-height: 20rem;
  }
}
@media (min-width: 1024px) {
  .features__info::after {
    width: 60%;
    height: 85%;
  }
}
.features__picture {
  grid-column: 2/3;
  grid-row: 1/2;
  justify-self: center;
}
.features__texts {
  margin-top: 6.25em;
  text-align: center;
  grid-column: 2/3;
}
@media (min-width: 1024px) {
  .features__texts {
    grid-column: 3/4;
    margin: 0;
    align-self: center;
    text-align: initial;
    margin-left: 2.5em;
  }
}
@media (min-width: 1200px) {
  .features__texts {
    margin-left: 5em;
  }
}
.features__img {
  width: 100%;
  height: 100%;
  display: block;
}
@media (min-width: 1024px) {
  .features__img {
    object-fit: contain;
  }
}
.features__cta {
  display: inline-block;
  padding: 0.625em 1.25em;
  background-color: hsl(231, 69%, 60%);
  color: hsl(0, 0%, 100%);
  text-decoration: none;
  border-radius: 0.3125rem;
  box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.2);
}

.download {
  width: 90%;
  max-width: 75rem;
  margin: 0 auto;
  padding-top: 7.5em;
}
.download__heading {
  text-align: center;
}
.download__title {
  color: hsl(229, 31%, 21%);
}
@media (min-width: 1024px) {
  .download__title--main {
    font-size: 1.875rem;
  }
}
.download__paragraph {
  color: hsl(229, 8%, 60%);
  margin: 1.25em 0;
  line-height: 1.7;
}
@media (min-width: 1024px) {
  .download__paragraph {
    max-width: 31.25rem;
    margin: 0 auto;
  }
}
.download__options {
  margin-top: 2.5em;
}
@media (min-width: 768px) {
  .download__options {
    display: flex;
    flex-wrap: wrap;
    gap: 1.25em;
    align-items: flex-start;
    justify-content: space-evenly;
  }
}
.download__option {
  max-width: 18.75em;
  margin: 0 auto;
  margin-bottom: 1.875em;
  box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.2);
  border-radius: 0.3125rem;
  text-align: center;
  padding: 2.5em 0 1.875em;
}
@media (min-width: 768px) {
  .download__option {
    margin: 0;
  }
  .download__option--2 {
    margin-top: 2.5em;
  }
  .download__option--3 {
    margin-top: 5em;
  }
}
.download__title {
  color: hsl(229, 31%, 21%);
  padding: 1.875em 0 0.625em;
}
.download__text {
  color: hsl(229, 8%, 60%);
}
.download__dots {
  width: 100%;
  padding: 2.5em 0 1.25em;
}
.download__cta {
  display: block;
  background-color: hsl(231, 69%, 60%);
  text-decoration: none;
  color: hsl(0, 0%, 100%);
  padding: 0.9375em 0;
  width: 90%;
  margin: 0 auto;
  border-radius: 0.3125rem;
  font-weight: 500;
  box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.2);
  border: 2px solid transparent;
  transition: all 0.3s;
}
.download__cta:hover {
  background-color: transparent;
  border: 2px solid hsl(231, 69%, 60%);
  color: hsl(231, 69%, 60%);
}

.faq {
  width: 90%;
  max-width: 75rem;
  margin: 0 auto;
  padding-top: 9.375em;
  padding-bottom: 6.25em;
  text-align: center;
}
@media (min-width: 768px) {
  .faq {
    max-width: 37.5rem;
  }
}
.faq__heading {
  text-align: center;
}
.faq__title {
  color: hsl(229, 31%, 21%);
  width: 90%;
  margin: 0 auto;
  line-height: 1.5;
}
@media (min-width: 1024px) {
  .faq__title {
    font-size: 1.875rem;
  }
}
.faq__paragraph {
  color: hsl(229, 8%, 60%);
  margin: 1.25em 0;
  line-height: 1.7;
}
@media (min-width: 1024px) {
  .faq__paragraph {
    width: 80%;
    margin: 1.25em auto;
  }
}
.faq__list {
  list-style-type: none;
  margin-top: 3.125em;
}
.faq__item {
  overflow: hidden;
}
.faq__question {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.25em 0;
  border-bottom: 1px solid hsl(229, 8%, 60%);
  cursor: pointer;
}
.faq__question:hover .faq__name {
  color: hsl(0, 94%, 66%);
}
.faq__name {
  color: hsl(229, 31%, 21%);
  font-weight: 500;
  transition: color 0.3s;
  text-align: left;
}
.faq__icon {
  font-size: 2.25rem;
  color: hsl(231, 69%, 60%);
  transition: transform 0.4s;
}
.faq__answer {
  height: 0;
  transition: height 0.4s;
}
.faq__content {
  padding: 1.25em 0;
  color: hsl(229, 8%, 60%);
  line-height: 2;
  text-align: left;
}
.faq__cta {
  display: inline-block;
  margin-top: 3.125em;
  background-color: hsl(231, 69%, 60%);
  color: hsl(0, 0%, 100%);
  text-decoration: none;
  padding: 0.9375em 1.25em;
  border-radius: 0.3125rem;
}
.faq__item.active .faq__question .faq__icon {
  color: hsl(0, 94%, 66%);
  transform: rotate(180deg);
}

.contact {
  background-color: hsl(231, 69%, 60%);
}
.contact__container {
  width: 90%;
  max-width: 75rem;
  margin: 0 auto;
  padding-top: 3.75em;
  padding-bottom: 2.5em;
  text-align: center;
}
@media (min-width: 768px) {
  .contact__container {
    max-width: 37.5rem;
  }
}
.contact__stats {
  text-transform: uppercase;
  color: hsl(0, 0%, 100%);
  letter-spacing: 3px;
  font-size: 0.875rem;
}
.contact__heading {
  margin: 0.625em 0 1.25em;
  color: hsl(0, 0%, 100%);
  font-weight: 400;
}
@media (min-width: 1024px) {
  .contact__heading {
    width: 70%;
    margin: 0.625em auto 1.25em;
    font-weight: 500;
    font-size: 1.875rem;
  }
}
@media (min-width: 1024px) {
  .contact__form {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
}
.contact__formgroup {
  width: 100%;
  position: relative;
}
@media (min-width: 1024px) {
  .contact__formgroup {
    width: 65%;
  }
}
.contact__formgroup.error .contact__input {
  border: 2px solid hsl(0, 94%, 66%);
}
.contact__formgroup.error .contact__icon {
  visibility: visible;
}
.contact__input {
  display: block;
  width: 100%;
  height: 3.3125rem;
  padding: 0.9375em 3.125em 0.9375em 0.9375em;
  border: none;
  outline: none;
  border-radius: 0.3125rem;
  font-family: inherit;
  font-size: 1rem;
  box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.2);
}
.contact__icon {
  visibility: hidden;
  position: absolute;
  top: 0.875rem;
  right: 1.25rem;
  color: hsl(0, 94%, 66%);
  font-size: 1.5rem;
  font-weight: 500;
}
.contact__cta {
  display: block;
  width: 100%;
  height: 3.3125rem;
  margin-top: 1.25em;
  padding: 0.9375em 0;
  border: none;
  outline: none;
  border-radius: 0.3125rem;
  background-color: hsl(0, 94%, 66%);
  color: hsl(0, 0%, 100%);
  font-family: inherit;
  font-size: 1rem;
  font-weight: 500;
  box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.2);
  border: 2px solid transparent;
  cursor: pointer;
  transition: all 0.3s;
}
@media (min-width: 1024px) {
  .contact__cta {
    margin: 0;
    width: 30%;
    text-align: center;
    padding: 0.9375em;
  }
}
.contact__cta:hover {
  background-color: hsl(0, 0%, 100%);
  color: hsl(0, 94%, 66%);
  border: 2px solid hsl(0, 94%, 66%);
}
.contact__error {
  background-color: hsl(0, 94%, 66%);
  padding: 0.625em 0.9375em;
  border-bottom-left-radius: 0.3125rem;
  border-bottom-right-radius: 0.3125rem;
  text-align: initial;
  font-size: 0.8125rem;
  color: hsl(0, 0%, 100%);
  font-weight: 500;
  font-style: italic;
}

.footer {
  background-color: hsl(229, 31%, 21%);
}
.footer__container {
  width: 90%;
  max-width: 75rem;
  margin: 0 auto;
  padding-top: 2.5em;
  padding-bottom: 1.875em;
  text-align: center;
}
@media (min-width: 1024px) {
  .footer__container {
    display: flex;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .footer__links {
    display: flex;
    align-items: center;
    gap: 1.875em;
    margin-left: 5em;
  }
}
.footer__link {
  display: block;
  text-decoration: none;
  color: hsl(0, 0%, 100%);
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 0.875rem;
  margin: 1.875em 0;
  transition: color 0.3s;
}
@media (min-width: 1024px) {
  .footer__link {
    margin: 0;
  }
}
.footer__link:hover {
  color: hsl(0, 94%, 66%);
}
.footer__social {
  color: hsl(0, 0%, 100%);
  font-size: 2.125rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.625em;
  cursor: pointer;
}
@media (min-width: 1024px) {
  .footer__social {
    margin-left: auto;
    font-size: 1.875rem;
  }
}
.footer__social i:hover {
  transition: color 0.3s;
  color: hsl(0, 94%, 66%);
}