@use 'base/mixins' as m;
@use 'base/variables' as v;

.faq{
    @include m.container(150);
    padding-bottom: m.em(100);
    text-align: center;

    @include m.tablet{
        max-width: m.rem(600);
    }

    &__heading{
        text-align: center;
    }

    &__title{
        color: v.$very-dark-blue;
        width: 90%;
        margin: 0 auto;
        line-height: 1.5;
        
        @include m.desktop{
            font-size: m.rem(30);
        }
    }

    &__paragraph{
        color: v.$grayish-blue;
        margin: m.em(20) 0;
        line-height: 1.7;

        @include m.desktop{
            width: 80%;
            margin: m.em(20) auto;
        }
    }

    &__list{
        list-style-type: none;
        margin-top: m.em(50);
    }

    &__item{
        overflow: hidden;
    }

    &__question{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: m.em(20) 0;
        border-bottom: 1px solid v.$grayish-blue;
        cursor: pointer;

        &:hover{
            .faq__name{
                color: v.$soft-Red;
            }
        }
    }

    &__name{
        color: v.$very-dark-blue;
        font-weight: 500;
        transition: color .3s;
        text-align: left;
    }

    &__icon{
        font-size: m.rem(36);
        color: v.$soft-blue;
        transition: transform .4s;
    }

    &__answer{
        height: 0;
        transition: height .4s;
    }

    &__content{
        padding: m.em(20) 0;
        color: v.$grayish-blue;
        line-height: 2; 
        text-align: left
    }

    &__cta{
        display: inline-block;
        margin-top: m.em(50);
        background-color: v.$soft-blue;
        color: v.$white;
        text-decoration: none;
        padding: m.em(15) m.em(20);
        border-radius: m.rem(5);
    }
    
    &__item.active{
        .faq__question{
            .faq__icon{
                color: v.$soft-Red;
                transform: rotate(180deg);
            }
        }
    }

}